@media screen and (max-width:1250px){
.body{overflow:hidden}
.header-wrapper{position:fixed;width:calc(100% - 24px);top:0;left:12px;padding:12px;border:1px solid #fff;border-bottom:none}
.header{justify-content:space-between}
.logotype{display:none}
.header__logotype{width:min-content;flex-wrap:wrap;justify-content:center}
.header__logotype-text{width:max-content}
.logotype__inner{width:fit-content;display:flex;justify-content:center}
.socialmedia-language__wrapper{justify-content:flex-end;width:fit-content}
.socialmedia-wrapper{display:none}
.circular-menu{width:45px;height:45px}
.circular-menu__item{width:40px;height:40px}
.SmallDeviceBurger{width:45px;height:45px;display:flex;flex-wrap:wrap;flex-direction:column;justify-content:center;align-items:center}
.navigate .socialmedia-wrapper{display:block;position:absolute;top:10px;right:45px}
.navigate .SmallDeviceBurger[data-isopen=true] .BurgerLine:nth-child(2){opacity:0}
.navigate .SmallDeviceBurger[data-isopen=true] .BurgerLine:nth-child(1){position:absolute;transform:rotateZ(-45deg);transition:.4s}
.navigate .SmallDeviceBurger[data-isopen=true] .BurgerLine:nth-child(3){position:absolute;transform:rotateZ(45deg);transition:.4s}
.SmallDeviceBurger .BurgerLine{width:90%;height:2px;background:#fff;margin-bottom:8px;border-radius:12px;transition:.4s;transform-origin:center}
.navigate{position:fixed;left:0;top:0;z-index:10;overflow:hidden;width:100%;transform:translateX(-100%);background:#2c2c2c;transition:ease .4s}
.inner-navigate{position:absolute;right:25px;top:55px}
.navigate .SmallDeviceBurger{width:30px;height:30px;transform-origin:center;opacity:0}
.navigate .SmallDeviceBurger .BurgerLine{width:90%;background:#fff;margin-bottom:8px;border-radius:12px;transition:.4s;transform-origin:center}
.navigate[data-isopen=true] .SmallDeviceBurger{position:fixed;top:45px;left:25px;opacity:1;transition:.4s}
.navigate[data-isopen=true]{transform:translateX(0);z-index:10}
.navigate-item{flex-wrap:wrap-reverse}
.pages-wrapper{overflow:hidden;border-left:1px solid #fff;border-right:1px solid #fff;padding:0 12px}
.container-wrapper:first-child{padding-top:140px}
.container-wrapper{padding:0;padding-top:130px}
.container-block__advantages{width:100%;justify-content:center}
.container-block__advantages .title{justify-content:center}
.container-block__advantages .title-button__reg{margin:0;margin-top:20px}
.container,.container-block,.container-content,.container-text,.content-wrapper,.pages-wrapper{width:100%}
.container__info-card-title{padding-left:0;padding-bottom:0;width:fit-content;height:fit-content;justify-content:flex-start}
.container__info-card-title div:first-child{font-size:21px;width:100%}
.container__info-card-title div:last-child{margin-left:20px;margin-bottom:20px}
.title{font-size:24px}
.container-block__dynamic .title{flex-wrap:wrap;order:-1}
.container-block__dynamic .container-item{padding-right:0}
.container-block__dynamic-graph-wrpaper{display:none}
.container-block__dynamic .info__page-list{width:100%}
.container-block__advantages .title-button__reg{display:none}
.container-block__advantages .title .title-text{line-height:25px!important;font-size:21px;padding:0 15px;word-wrap:break-word;margin-bottom:15px}
.container-block__advantages .title{justify-content:center;display:flex;flex-wrap:wrap}
.container-block__advantages .title .subtitle{justify-content:center;font-weight:lighter;text-align:center;position:inherit}
.info__page-list-image{width:auto;height:85px}
.container__info-card{padding:0;padding-bottom:45px}
.toggle-item{width:140px;font-size:12px;font-weight:lighter}
.container-block__dynamic-graph-wrpaper,.container-block__token-graph-wrpaper{width:100%;justify-content:center}
.container-token-content{width:100%;display:flex;transition:.4s;position:relative;flex-wrap:wrap}
.container-list{padding-top:30px;width:100%!important}
.doughnut-wrapper{width:140px;height:140px}
.doughnut-wrapper:before{width:70px;height:70px}
.doughnut{width:140px!important;height:140px!important}
.item-1,.item-2,.item-3,.item-4{position:absolute;color:#008bb7;font-size:9px;font-weight:700}
.item-1,.item-2,.item-3{width:min-content}
.item-1{display:flex;right:-35px;top:-15px;text-align:left}
.item-1 .item-percent{position:relative;bottom:-56px;left:60px;font-size:9px;color:#000}
.item-2{display:flex;right:-90px;top:20px}
.item-2 .item-percent{position:relative;bottom:-40px;left:23px;font-size:9px;color:#000}
.item-3{display:flex;right:-135px;bottom:75px;text-align:left}
.item-3 .item-percent{position:relative;bottom:-8px;left:-6px;font-size:9px;color:#000}
.item-4{width:80px;bottom:-15px;left:-10px;font-weight:700;font-size:14px;display:flex;flex-wrap:wrap;justify-content:flex-end;text-align:right;color:#fff}
.item-percent{color:#fff;width:42px;margin-right:12px}
.item-percent:last-child{margin-right:0}
.item-text{width:130px;display:block}
.container-content,.container-text{padding-right:0;font-size:14px;text-align:left}
.container-text,.title{z-index:2}
.container-content{line-height:24px}
.container-block{padding:0}
.article{padding-bottom:80px}
.article-title{font-size:18px}
.article-open-button{width:60px;justify-content:flex-start}
.article-text{padding-left:0;max-width:none;line-height:21px}
.planet-animation-outer{opacity:.4;position:absolute;top:-60px;z-index:1}
.planet-text{display:none}
.Clock{align-items:flex-start;justify-content:center;padding:0;padding-top:120px}
.hour{width:8px;height:44px;border-width:1px}
.table-wrapper{width:100%}
.table-outer-wrapper{flex-wrap:wrap}
.table-row-column{font-size:10px;width:33.33%}
.table-title-column{width:100%;padding:10px;font-size:9px}
.table-column:first-child>.table-row-column{padding-right:12px;word-break:break-all;text-align:right}
.table-column+.table-column:last-child>.table-row-column{padding-left:12px;word-break:break-all}
.table{width:100%;justify-content:flex-end}
.table .table-title-column::after{display:flex;content:"";position:absolute;right:0;bottom:0;background:#fff;width:2px;height:90%}
.table-column{width:33.33%}
.table-column .table-row-column{width:100%}
.table-row-column-inner{font-size:9px}
.table-aside .table-title-column{min-height:45px;padding:0;max-width:-moz-fit-content;max-width:fit-content}
.table-aside .table-title:first-child{position:relative;width:100%}
.table-aside .table-row .table-row-column{padding-left:0}
.container-block__logotype-wrapper{display:none}
.table-aside .table-row:last-child .table-row-column::after{content:"";display:flex;right:-1.7px;top:0;height:135%;width:2px;background:linear-gradient(#fff,transparent)}
.container-token-content{justify-content:center}
.form-wrapper{width:100%}
.form__fieldset{padding:10px;flex-wrap:wrap}
.form__fieldset>div{width:100%}
.scrollNextPage-svg{position:fixed;right:30px;bottom:20px;display:inline-flex;justify-content:center;align-items:center}
}
@media (min-width:560px) and (max-width:1250px){
.navigate{width:40%;height:95vh;border-right:2px solid rgba(44,44,44,.1);box-shadow:0 0 10px 10px rgba(0,0,0,.2);border-bottom-right-radius:50px}
.inner-navigate{position:absolute;left:35px;top:55px}
.navigate .SmallDeviceBurger{width:30px;height:30px;transform-origin:center;opacity:0;z-index:10}
.navigate .SmallDeviceBurger .BurgerLine{width:90%;background:#fff;margin-bottom:8px;border-radius:12px;transition:.4s;transform-origin:center}
.navigate[data-isopen=true] .SmallDeviceBurger{position:absolute;top:55px;right:25px;opacity:1;transition:.4s}
.navigate[data-isopen=true]{transform:translateX(0);z-index:10}
.navigate-item{flex-wrap:wrap-reverse}
.Clock{padding-top:0;align-items:center}
.container__info-card-title{justify-content:center}
.container__info-card-title div:first-child{justify-content:center}
.container__info-card-title div:last-child{justify-content:center;margin:20px 0}
.container-content{width:100%;justify-content:center}
.container-item{width:51%}
#page-7 .info__page-list .container-item:last-child{display:flex;justify-content:center}
.info__page-list .container-item:last-child .title-button__reg{width:90%}
.container-text{justify-content:center}
.table-title-column{max-width:none;font-size:14px}
.table-aside .table-title-column{width:100%;max-width:none;font-size:14px}
.table-column:first-child .table-row-column-inner{text-align:right}
.table-column:last-child .table-row-column-inner{text-align:left}
.table-column .table-row-column-inner{width:100%;font-size:14px;text-align:center}
.table-aside .table-column .table-row-column-inner{width:100%;font-size:14px;text-align:right;padding-right:12px}
.table-aside{padding-right:32px}
.form__fieldset{padding:20px 23%}
.terms{padding-bottom:40px}
.container-block__dynamic-graph-wrpaper{display:flex;width:80%;order:-1;margin-bottom:0}
.container-block__dynamic .container-item{width:80%;display:flex}
.container-block__dynamic .container-item br{display:none}
.container-block__dynamic-graph-wrpaper{height:fit-content!important}
.form-wrapper{width:80%}
#page-0 .logotype{display:flex!important}
#page-0 .container-text{width:50%;padding-right:15px}
#page-0 .container-block{flex-wrap:nowrap}
#page-2 .container-item{width:51%;margin-left:190px}
.article-open-button{width:fit-content}
.container-block__dynamic-graph-wrpaper,.container-block__token-graph-wrpaper{width:100%;justify-content:center}
.container-token-content{width:100%;display:flex;transition:.4s;position:relative;flex-wrap:wrap}
.container-list{padding-top:30px;width:100%!important}
.doughnut-wrapper{width:180px;height:180px}
.doughnut-wrapper:before{width:110px;height:110px}
.doughnut{width:180px!important;height:180px!important}
.item-1,.item-2,.item-3,.item-4{position:absolute;color:#008bb7;font-size:12px;font-weight:700}
.item-1,.item-2,.item-3{width:fit-content}
.item-1 .item-text,.item-2 .item-text,.item-3 .item-text{text-shadow:0 0 1px #000}
.item-1{display:flex;right:-35px;top:-15px;text-align:left}
.item-1 .item-text{width:fit-content}
.item-1 .item-percent{position:relative;bottom:-56px;left:60px;font-size:9px;color:#000}
.item-2{display:flex;right:-90px;top:20px}
.item-2 .item-percent{position:relative;bottom:-48px;left:20px;font-size:9px;color:#000}
.item-3{display:flex;right:-135px;bottom:75px;text-align:left}
.item-3 .item-percent{position:relative;bottom:12px;left:-6px;font-size:9px;color:#000}
.item-4{width:80px;bottom:-15px;left:-10px;font-weight:700;font-size:14px;display:flex;flex-wrap:wrap;justify-content:flex-end;text-align:right;color:#fff}
}