body{background:#2c2c2c}
.registration__wrapper{width:100%;max-width:1560px;height:100%;margin:0 auto;display:flex;justify-content:center;background:url(logoLarge.png) #2c2c2c;background-size:40%;background-repeat:no-repeat;background-position:center 120px;color:#fff;font-family:sans-serif}
.registration__inner-wrapper{width:100%;height:100%;padding-top:6vh;display:flex;justify-content:center;align-content:flex-start;flex-wrap:wrap;background:rgba(48,48,48,.9)}
.registration-title__wrapper{width:100%;display:flex;flex-wrap:wrap;justify-content:center;align-items:flex-start}
.registration__title{width:100%;text-align:center;font-weight:lighter;padding-bottom:18px}
.registration__subtitle{font-size:16px}
.registration__page-switcher{width:40%;padding:40px 0;display:flex;justify-content:space-between}
.page-swicher__item{text-align:center;font-size:18px;font-weight:lighter;cursor:pointer;position:relative}
.item-inner{display:inline;position:relative}
.item-inner:before{content:'';position:absolute;bottom:-4px;left:0;width:0;height:1.5px;background:#ebbd25;transition:.4s}
.page-swicher__item:first-child{text-align:left;padding-left:8%}
.page-swicher__item:last-child{text-align:right;padding-right:8%}
.item-inner:hover:before,.page-swicher__item[data-active="1"] .item-inner:before{width:80%}
.registration-form{width:100%;display:flex;justify-content:center;flex-wrap:wrap;position:relative}
.registration-page[data-active="0"]{opacity:0;z-index:0}
.registration-page[data-active="0"] .registration-input__wrapper{transform:translateY(-30%);transition:.4s}
.registration-page{width:100%;height:fit-content;min-height:450px;display:flex;justify-content:center;align-items:flex-start;position:absolute;transition:.6s;z-index:3}
.registration-inner-page{width:40%;display:flex;justify-content:center;align-items:flex-start;flex-wrap:wrap}
.registration-input__wrapper{position:relative;width:50%;height:90px;transition:.4s}
.registration-image{position:absolute;left:15%;top:50%;transform:translateY(-50%);border-radius:100%;border:1px solid #fff;padding:6px}
.registration-input__wrapper label{position:absolute;left:33%;bottom:32px;transition:.4s;z-index:1}
.registration-input__wrapper input:focus~label{transform:translateY(-18px);font-size:12px;color:rgba(250,250,250,.4)}
.registration-input__wrapper input{position:absolute;left:50%;top:50%;z-index:2;transform:translateX(-50%) translateY(-50%);width:70%;height:45px;padding-top:16px;padding-right:15px;padding-bottom:4px;padding-left:55px;border-radius:25px;border:1px solid #fff;line-height:26px;color:#fff;font-size:18px;background:0 0;outline:0}
.registration__send-btn-wrapper{margin-top:55px;background:linear-gradient(45deg,#f1c225,#03789a);border-radius:25px;opacity:.6;padding:4px;width:40%}
.registration__send-btn{background:#ddd;color:#2d2d2c;width:100%;border:none;border-radius:25px;padding:10px 20px;font-size:18px;cursor:pointer}
.registration__send-btn-wrapper-90{width:90%;display:flex;justify-content:center}
.registration__send-btn-wrapper-90 .registration__send-btn-wrapper{margin-top:15px}